import React, { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import { Button, Card, Checkbox, Col, ConfigProvider, DatePicker, Row, Select, Space, Typography } from 'antd';
import dayjs from 'dayjs';
import customParseFormat from 'dayjs/plugin/customParseFormat';
import 'dayjs/locale/ru';
import ruRU from 'antd/locale/ru_RU';

dayjs.extend(customParseFormat);
dayjs.locale('ru');

const dateFormat = 'DD.MM.YYYY';

const parseBool = (value) => value === 'true' || value === '1' || value === 'on';

const getInputByName = (name) => document.querySelector(`input[name="${name}"]`);
const getCheckboxByName = (name) => document.querySelector(`input[type="checkbox"][name="${name}"]`);

const readInputValue = (name) => {
  const el = getInputByName(name);
  return el ? el.value : '';
};

const readCheckboxState = (name) => {
  const el = getCheckboxByName(name);
  return {
    checked: !!(el && el.checked),
    disabled: !!(el && el.disabled),
  };
};

const getConfigValue = (id) => {
  const el = document.getElementById(id);
  return el ? el.value : '';
};

const addQueryParam = (url, key, value) => {
  if (!key) return url;
  const sep = url.indexOf('?') >= 0 ? '&' : '?';
  return `${url}${sep}${encodeURIComponent(key)}=${encodeURIComponent(value || '')}`;
};

const decodeJsString = (value) =>
  value.replace(/\\\\/g, '\\').replace(/\\'/g, "'").replace(/\\n/g, '\n').replace(/\\r/g, '\r');

const parseListOptions = (html) => {
  const doc = new DOMParser().parseFromString(html, 'text/html');
  const rows = Array.from(doc.querySelectorAll('tr'));
  const options = [];
  rows.forEach((row) => {
    const onClick = row.getAttribute('onclick') || '';
    if (!onClick.includes('ItemSelected')) return;
    const args = [];
    const re = /'((?:\\'|[^'])*)'/g;
    let match;
    while ((match = re.exec(onClick)) !== null) {
      args.push(decodeJsString(match[1]));
    }
    const labelFromRow = (row.querySelector('td')?.textContent || '').trim();
    const value = args[0] || labelFromRow;
    const label = args[1] || labelFromRow || args[0] || '';
    if (!value && !label) return;
    options.push({ value: String(value), label: String(label) });
  });
  return options;
};

const fetchListOptions = async (url, filterParam, query) => {
  if (!url) return [];
  const finalUrl = filterParam ? addQueryParam(url, filterParam, query) : url;
  const res = await fetch(finalUrl, { credentials: 'same-origin' });
  const html = await res.text();
  return parseListOptions(html);
};

const buildSelectValue = (value, label) => {
  if (!value && !label) return null;
  return { value: value || label, label: label || value };
};

const MarginDevApp = () => {
  const labels = useMemo(() => {
    const container = document.getElementById('margin-dev-labels');
    const getLabel = (key, fallback) => {
      const el = container ? container.querySelector(`[data-key="${key}"]`) : null;
      return (el && el.textContent ? el.textContent.trim() : fallback) || fallback || '';
    };
    return {
      period: getLabel('period', ''),
      start: getLabel('start', ''),
      end: getLabel('end', ''),
      dateFormat: getLabel('date_format', ' : ..'),
      formBy: getLabel('form_by', ' '),
      aspectBy: getLabel('aspect_by', ''),
      user: getLabel('user', ''),
      department: getLabel('department', ''),
      contractor: getLabel('contractor', ''),
      stuffCategory: getLabel('stuff_category', ''),
      route: getLabel('route', ''),
      viewColumns: getLabel('view_columns', ''),
      options: getLabel('options', ''),
      onlyTotal: getLabel('only_total', ' '),
      itogBySpec: getLabel('itog_by_spec', '  '),
      itogByUser: getLabel('itog_by_user', '  '),
      itogByProduct: getLabel('itog_by_product', '  '),
      getNotBlock: getLabel('get_not_block', ' '),
      buttonClear: getLabel('button_clear', ''),
      buttonForm: getLabel('button_form', ''),
      buttonExcel: getLabel('button_excel', 'Excel'),
      columns: [
        { key: 'view_contractor', label: getLabel('view_contractor', '') },
        { key: 'view_country', label: getLabel('view_country', '') },
        { key: 'view_contract', label: getLabel('view_contract', '') },
        { key: 'view_stuff_category', label: getLabel('view_stuff_category', '') },
        { key: 'view_shipping', label: getLabel('view_shipping', '') },
        { key: 'view_payment', label: getLabel('view_payment', '') },
        { key: 'view_transport', label: getLabel('view_transport', '') },
        { key: 'view_transport_sum', label: getLabel('view_transport_sum', ' ') },
        { key: 'view_custom', label: getLabel('view_custom', '') },
        { key: 'view_other_sum', label: getLabel('view_other_sum', ' ') },
        { key: 'view_montage_sum', label: getLabel('view_montage_sum', ' ') },
        { key: 'view_montage_time', label: getLabel('view_montage_time', ' ') },
        { key: 'view_montage_cost', label: getLabel('view_montage_cost', ' ') },
        { key: 'view_update_sum', label: getLabel('view_update_sum', ' ') },
        { key: 'view_summ_zak', label: getLabel('view_summ_zak', ' ') },
        { key: 'view_koeff', label: getLabel('view_koeff', '') },
        { key: 'view_user', label: getLabel('view_user', '') },
        { key: 'view_department', label: getLabel('view_department', '') },
      ],
    };
  }, []);

  const showForChiefDep = parseBool(readInputValue('showForChiefDep'));
  const readOnlyForManager = parseBool(readInputValue('readOnlyForManager'));

  const usersUrl = getConfigValue('margin-dev-users-url');
  const departmentsUrl = getConfigValue('margin-dev-departments-url');
  const contractorsUrl = getConfigValue('margin-dev-contractors-url');
  const stuffCategoriesUrl = getConfigValue('margin-dev-stuff-categories-url');
  const routesUrl = getConfigValue('margin-dev-routes-url');
  const gridUrl = getConfigValue('margin-dev-grid-url');
  const actionGenerate = getConfigValue('margin-dev-action-generate');
  const actionClean = getConfigValue('margin-dev-action-clean');
  const actionExcel = getConfigValue('margin-dev-action-excel');

  const initialDateBegin = readInputValue('date_begin');
  const initialDateEnd = readInputValue('date_end');

  const [dateBegin, setDateBegin] = useState(initialDateBegin);
  const [dateEnd, setDateEnd] = useState(initialDateEnd);

  const [userValue, setUserValue] = useState(
    buildSelectValue(readInputValue('user.usr_id'), readInputValue('user.userFullName'))
  );
  const [departmentValue, setDepartmentValue] = useState(
    buildSelectValue(readInputValue('department.id'), readInputValue('department.name'))
  );
  const [contractorValue, setContractorValue] = useState(
    buildSelectValue(readInputValue('contractor.id'), readInputValue('contractor.name'))
  );
  const [stuffCategoryValue, setStuffCategoryValue] = useState(
    buildSelectValue(readInputValue('stuffCategory.id'), readInputValue('stuffCategory.name'))
  );
  const [routeValue, setRouteValue] = useState(
    buildSelectValue(readInputValue('route.id'), readInputValue('route.name'))
  );

  const initialUserAspect = readCheckboxState('user_aspect');
  const initialDepartmentAspect = readCheckboxState('department_aspect');
  const initialContractorAspect = readCheckboxState('contractor_aspect');
  const initialStuffCategoryAspect = readCheckboxState('stuff_category_aspect');
  const initialRouteAspect = readCheckboxState('route_aspect');

  const serverAspectDisabled = useMemo(
    () => ({
      user: initialUserAspect.disabled,
      department: initialDepartmentAspect.disabled,
      contractor: initialContractorAspect.disabled,
      stuffCategory: initialStuffCategoryAspect.disabled,
      route: initialRouteAspect.disabled,
    }),
    [
      initialUserAspect.disabled,
      initialDepartmentAspect.disabled,
      initialContractorAspect.disabled,
      initialStuffCategoryAspect.disabled,
      initialRouteAspect.disabled,
    ]
  );

  const [userAspect, setUserAspect] = useState(initialUserAspect.checked);
  const [departmentAspect, setDepartmentAspect] = useState(initialDepartmentAspect.checked);
  const [contractorAspect, setContractorAspect] = useState(initialContractorAspect.checked);
  const [stuffCategoryAspect, setStuffCategoryAspect] = useState(initialStuffCategoryAspect.checked);
  const [routeAspect, setRouteAspect] = useState(initialRouteAspect.checked);
  const [aspectDisabled, setAspectDisabled] = useState({
    user: false,
    department: false,
    contractor: false,
    stuffCategory: false,
    route: false,
  });

  const initialOnlyTotal = readCheckboxState('onlyTotal');
  const initialItogBySpec = readCheckboxState('itog_by_spec');
  const initialItogByUser = readCheckboxState('itog_by_user');
  const initialItogByProduct = readCheckboxState('itog_by_product');
  const initialGetNotBlock = readCheckboxState('get_not_block');

  const [onlyTotal, setOnlyTotal] = useState(initialOnlyTotal.checked);
  const [itogBySpec, setItogBySpec] = useState(initialItogBySpec.checked);
  const [itogByUser, setItogByUser] = useState(initialItogByUser.checked);
  const [itogByProduct, setItogByProduct] = useState(initialItogByProduct.checked);
  const [getNotBlock, setGetNotBlock] = useState(initialGetNotBlock.checked);

  const initialColumns = labels.columns.reduce((acc, item) => {
    acc[item.key] = readCheckboxState(item.key).checked;
    return acc;
  }, {});

  const viewOtherSumDisabled = readCheckboxState('view_other_sum').disabled;

  const [viewColumns, setViewColumns] = useState(initialColumns);

  const [userOptions, setUserOptions] = useState([]);
  const [userLoading, setUserLoading] = useState(false);
  const [departmentOptions, setDepartmentOptions] = useState([]);
  const [departmentLoading, setDepartmentLoading] = useState(false);
  const [contractorOptions, setContractorOptions] = useState([]);
  const [contractorLoading, setContractorLoading] = useState(false);
  const [stuffCategoryOptions, setStuffCategoryOptions] = useState([]);
  const [stuffCategoryLoading, setStuffCategoryLoading] = useState(false);
  const [routeOptions, setRouteOptions] = useState([]);
  const [routeLoading, setRouteLoading] = useState(false);

  const cacheRef = useRef(new Map());

  const loadOptions = useCallback(async (key, url, filterParam, query, setOptions, setLoading) => {
    if (!url) return;
    const cacheKey = `${key}|${query}`;
    if (cacheRef.current.has(cacheKey)) {
      setOptions(cacheRef.current.get(cacheKey));
      return;
    }
    setLoading(true);
    try {
      const options = await fetchListOptions(url, filterParam, query);
      cacheRef.current.set(cacheKey, options);
      setOptions(options);
    } catch (e) {
      setOptions([]);
    } finally {
      setLoading(false);
    }
  }, []);

  const loadUsers = useCallback(
    (query) => loadOptions('users', usersUrl, 'filter', query, setUserOptions, setUserLoading),
    [loadOptions, usersUrl]
  );
  const loadDepartments = useCallback(
    () => loadOptions('departments', departmentsUrl, '', '', setDepartmentOptions, setDepartmentLoading),
    [loadOptions, departmentsUrl]
  );
  const loadContractors = useCallback(
    (query) => loadOptions('contractors', contractorsUrl, 'filter', query, setContractorOptions, setContractorLoading),
    [loadOptions, contractorsUrl]
  );
  const loadStuffCategories = useCallback(
    (query) =>
      loadOptions('stuffCategories', stuffCategoriesUrl, 'filter', query, setStuffCategoryOptions, setStuffCategoryLoading),
    [loadOptions, stuffCategoriesUrl]
  );
  const loadRoutes = useCallback(
    () => loadOptions('routes', routesUrl, '', '', setRouteOptions, setRouteLoading),
    [loadOptions, routesUrl]
  );

  const setHiddenValue = (name, value) => {
    const el = getInputByName(name);
    if (el) el.value = value || '';
  };

  const setHiddenCheckbox = (name, checked, disabled) => {
    const el = getCheckboxByName(name);
    if (!el) return;
    el.checked = !!checked;
    if (typeof disabled === 'boolean') {
      el.disabled = disabled;
    }
  };

  const hasAnySelection = Boolean(
    (userValue && userValue.label) ||
      (departmentValue && departmentValue.label) ||
      (contractorValue && contractorValue.label) ||
      (stuffCategoryValue && stuffCategoryValue.label) ||
      (routeValue && routeValue.label)
  );

  useEffect(() => {
    if (onlyTotal && !hasAnySelection) {
      setOnlyTotal(false);
    }
  }, [onlyTotal, hasAnySelection]);

  useEffect(() => {
    if (onlyTotal && itogBySpec) {
      setItogBySpec(false);
    }
  }, [onlyTotal, itogBySpec]);

  useEffect(() => {
    if (!itogBySpec && itogByUser) {
      setItogByUser(false);
    }
  }, [itogBySpec, itogByUser]);

  useEffect(() => {
    if (!itogByUser && itogByProduct) {
      setItogByProduct(false);
    }
  }, [itogByUser, itogByProduct]);

  useEffect(() => {
    setHiddenValue('date_begin', dateBegin);
    setHiddenValue('date_end', dateEnd);
  }, [dateBegin, dateEnd]);

  useEffect(() => {
    setHiddenValue('user.userFullName', userValue ? userValue.label : '');
    setHiddenValue('user.usr_id', userValue ? userValue.value : '');
    setHiddenValue('department.name', departmentValue ? departmentValue.label : '');
    setHiddenValue('department.id', departmentValue ? departmentValue.value : '');
    setHiddenValue('contractor.name', contractorValue ? contractorValue.label : '');
    setHiddenValue('contractor.id', contractorValue ? contractorValue.value : '');
    setHiddenValue('stuffCategory.name', stuffCategoryValue ? stuffCategoryValue.label : '');
    setHiddenValue('stuffCategory.id', stuffCategoryValue ? stuffCategoryValue.value : '');
    setHiddenValue('route.name', routeValue ? routeValue.label : '');
    setHiddenValue('route.id', routeValue ? routeValue.value : '');
  }, [userValue, departmentValue, contractorValue, stuffCategoryValue, routeValue]);

  useEffect(() => {
    const finalAspectDisabled = {
      user: serverAspectDisabled.user || aspectDisabled.user,
      department: serverAspectDisabled.department || aspectDisabled.department,
      contractor: serverAspectDisabled.contractor || aspectDisabled.contractor,
      stuffCategory: serverAspectDisabled.stuffCategory || aspectDisabled.stuffCategory,
      route: serverAspectDisabled.route || aspectDisabled.route,
    };
    setHiddenCheckbox('user_aspect', userAspect, finalAspectDisabled.user);
    setHiddenCheckbox('department_aspect', departmentAspect, finalAspectDisabled.department);
    setHiddenCheckbox('contractor_aspect', contractorAspect, finalAspectDisabled.contractor);
    setHiddenCheckbox('stuff_category_aspect', stuffCategoryAspect, finalAspectDisabled.stuffCategory);
    setHiddenCheckbox('route_aspect', routeAspect, finalAspectDisabled.route);
  }, [
    userAspect,
    departmentAspect,
    contractorAspect,
    stuffCategoryAspect,
    routeAspect,
    aspectDisabled,
    serverAspectDisabled,
  ]);

  useEffect(() => {
    setHiddenCheckbox('onlyTotal', onlyTotal);
    setHiddenCheckbox('itog_by_spec', itogBySpec);
    setHiddenCheckbox('itog_by_user', itogByUser, !itogBySpec);
    setHiddenCheckbox('itog_by_product', itogByProduct, !itogByUser);
    setHiddenCheckbox('get_not_block', getNotBlock);
  }, [onlyTotal, itogBySpec, itogByUser, itogByProduct, getNotBlock]);

  useEffect(() => {
    labels.columns.forEach((item) => {
      const disabled = item.key === 'view_other_sum' ? viewOtherSumDisabled : undefined;
      setHiddenCheckbox(item.key, viewColumns[item.key], disabled);
    });
  }, [labels.columns, viewColumns, viewOtherSumDisabled]);

  const allAspectEnabled = useCallback(() => {
    setAspectDisabled({
      user: false,
      department: false,
      contractor: false,
      stuffCategory: false,
      route: false,
    });
  }, []);

  const handleUserChange = (value) => {
    setUserValue(value || null);
    setUserAspect(false);
    if (!showForChiefDep) {
      allAspectEnabled();
      setAspectDisabled((prev) => ({ ...prev, user: true }));
      setDepartmentValue(null);
      setContractorValue(null);
      setStuffCategoryValue(null);
    }
    setRouteValue(null);
  };

  const handleDepartmentChange = (value) => {
    setDepartmentValue(value || null);
    allAspectEnabled();
    setAspectDisabled((prev) => ({ ...prev, department: true }));
    setDepartmentAspect(false);
    setUserValue(null);
    setContractorValue(null);
    setStuffCategoryValue(null);
    setRouteValue(null);
  };

  const handleContractorChange = (value) => {
    setContractorValue(value || null);
    allAspectEnabled();
    setAspectDisabled((prev) => ({ ...prev, contractor: true }));
    setContractorAspect(false);
    setUserValue(null);
    setDepartmentValue(null);
    setStuffCategoryValue(null);
    setRouteValue(null);
  };

  const handleStuffCategoryChange = (value) => {
    setStuffCategoryValue(value || null);
    allAspectEnabled();
    setAspectDisabled((prev) => ({ ...prev, stuffCategory: true }));
    setStuffCategoryAspect(false);
    setUserValue(null);
    setDepartmentValue(null);
    setContractorValue(null);
    setRouteValue(null);
  };

  const handleRouteChange = (value) => {
    setRouteValue(value || null);
    allAspectEnabled();
    setAspectDisabled((prev) => ({ ...prev, route: true }));
    setRouteAspect(false);
    setUserValue(null);
    setDepartmentValue(null);
    setContractorValue(null);
    setStuffCategoryValue(null);
  };

  const handleAspectChange = (key, checked) => {
    if (!checked) {
      if (key === 'user') setUserAspect(false);
      if (key === 'department') setDepartmentAspect(false);
      if (key === 'contractor') setContractorAspect(false);
      if (key === 'stuffCategory') setStuffCategoryAspect(false);
      if (key === 'route') setRouteAspect(false);
      return;
    }
    setUserAspect(key === 'user');
    setDepartmentAspect(key === 'department');
    setContractorAspect(key === 'contractor');
    setStuffCategoryAspect(key === 'stuffCategory');
    setRouteAspect(key === 'route');
  };

  const canGenerate = Boolean(dateBegin && dateEnd && hasAnySelection);

  const submitAction = (url) => {
    if (!url) return;
    try {
      if (typeof window.lockForm === 'function' && window.lockForm()) return;
    } catch (e) {}
    try {
      if (typeof window.runProgress === 'function') window.runProgress();
    } catch (e) {}
    const form = document.forms && document.forms.length ? document.forms[0] : null;
    if (!form) return;
    const saved = form.action;
    form.action = url;
    form.submit();
    form.action = saved;
  };

  const generateExcel = (url) => {
    if (!url) return;
    const target = document.getElementById('for-insert');
    if (target) {
      target.innerHTML = `<iframe src="${url}" style="display:none"></iframe>`;
    }
  };

  return (
    <ConfigProvider locale={ruRU}>
      <div className="margin-dev-root">
        <Row gutter={[16, 16]}>
          <Col xs={24} md={12} xl={6}>
            <Card title={labels.period}>
              <Space direction="vertical" size={8} style={{ width: '100%' }}>
                <div>
                  <Typography.Text type="secondary">{labels.start}</Typography.Text>
                  <DatePicker
                    style={{ width: '100%' }}
                    format={dateFormat}
                    allowClear
                    value={dateBegin ? dayjs(dateBegin, dateFormat, true) : null}
                    onChange={(value, str) => setDateBegin(str || '')}
                  />
                </div>
                <div>
                  <Typography.Text type="secondary">{labels.end}</Typography.Text>
                  <DatePicker
                    style={{ width: '100%' }}
                    format={dateFormat}
                    allowClear
                    value={dateEnd ? dayjs(dateEnd, dateFormat, true) : null}
                    onChange={(value, str) => setDateEnd(str || '')}
                  />
                </div>
                <Typography.Text type="secondary">{labels.dateFormat}</Typography.Text>
              </Space>
            </Card>
          </Col>

          <Col xs={24} md={12} xl={8}>
            <Card title={labels.formBy}>
              <Space direction="vertical" size={12} style={{ width: '100%' }}>
                <div className="margin-dev-aspect-row">
                  <div className="margin-dev-aspect-field">
                    <Typography.Text type="secondary">{labels.user}</Typography.Text>
                    <Select
                      style={{ width: '100%' }}
                      labelInValue
                      allowClear
                      showSearch
                      value={userValue}
                      disabled={readOnlyForManager}
                      options={userOptions}
                      loading={userLoading}
                      filterOption={false}
                      onSearch={loadUsers}
                      onDropdownVisibleChange={(open) => {
                        if (open && userOptions.length === 0) loadUsers('');
                      }}
                      onChange={handleUserChange}
                      placeholder={labels.user}
                    />
                  </div>
                  <div className="margin-dev-aspect-checkbox">
                    <Checkbox
                      checked={userAspect}
                      disabled={serverAspectDisabled.user || aspectDisabled.user}
                      onChange={(e) => handleAspectChange('user', e.target.checked)}
                      aria-label={labels.user}
                    />
                  </div>
                </div>

                <div className="margin-dev-aspect-row">
                  <div className="margin-dev-aspect-field">
                    <Typography.Text type="secondary">{labels.department}</Typography.Text>
                    <Select
                      style={{ width: '100%' }}
                      labelInValue
                      allowClear
                      showSearch
                      value={departmentValue}
                      disabled={readOnlyForManager}
                      options={departmentOptions}
                      loading={departmentLoading}
                      onDropdownVisibleChange={(open) => {
                        if (open && departmentOptions.length === 0) loadDepartments();
                      }}
                      filterOption={(input, option) =>
                        option && option.label ? option.label.toLowerCase().includes(input.toLowerCase()) : false
                      }
                      onChange={handleDepartmentChange}
                      placeholder={labels.department}
                    />
                  </div>
                  <div className="margin-dev-aspect-checkbox">
                    <Checkbox
                      checked={departmentAspect}
                      disabled={serverAspectDisabled.department || aspectDisabled.department}
                      onChange={(e) => handleAspectChange('department', e.target.checked)}
                      aria-label={labels.department}
                    />
                  </div>
                </div>

                <div className="margin-dev-aspect-row">
                  <div className="margin-dev-aspect-field">
                    <Typography.Text type="secondary">{labels.contractor}</Typography.Text>
                    <Select
                      style={{ width: '100%' }}
                      labelInValue
                      allowClear
                      showSearch
                      value={contractorValue}
                      disabled={readOnlyForManager}
                      options={contractorOptions}
                      loading={contractorLoading}
                      filterOption={false}
                      onSearch={loadContractors}
                      onDropdownVisibleChange={(open) => {
                        if (open && contractorOptions.length === 0) loadContractors('');
                      }}
                      onChange={handleContractorChange}
                      placeholder={labels.contractor}
                    />
                  </div>
                  <div className="margin-dev-aspect-checkbox">
                    <Checkbox
                      checked={contractorAspect}
                      disabled={serverAspectDisabled.contractor || aspectDisabled.contractor}
                      onChange={(e) => handleAspectChange('contractor', e.target.checked)}
                      aria-label={labels.contractor}
                    />
                  </div>
                </div>

                <div className="margin-dev-aspect-row">
                  <div className="margin-dev-aspect-field">
                    <Typography.Text type="secondary">{labels.stuffCategory}</Typography.Text>
                    <Select
                      style={{ width: '100%' }}
                      labelInValue
                      allowClear
                      showSearch
                      value={stuffCategoryValue}
                      disabled={readOnlyForManager}
                      options={stuffCategoryOptions}
                      loading={stuffCategoryLoading}
                      filterOption={false}
                      onSearch={loadStuffCategories}
                      onDropdownVisibleChange={(open) => {
                        if (open && stuffCategoryOptions.length === 0) loadStuffCategories('');
                      }}
                      onChange={handleStuffCategoryChange}
                      placeholder={labels.stuffCategory}
                    />
                  </div>
                  <div className="margin-dev-aspect-checkbox">
                    <Checkbox
                      checked={stuffCategoryAspect}
                      disabled={serverAspectDisabled.stuffCategory || aspectDisabled.stuffCategory}
                      onChange={(e) => handleAspectChange('stuffCategory', e.target.checked)}
                      aria-label={labels.stuffCategory}
                    />
                  </div>
                </div>

                <div className="margin-dev-aspect-row">
                  <div className="margin-dev-aspect-field">
                    <Typography.Text type="secondary">{labels.route}</Typography.Text>
                    <Select
                      style={{ width: '100%' }}
                      labelInValue
                      allowClear
                      showSearch
                      value={routeValue}
                      disabled={readOnlyForManager}
                      options={routeOptions}
                      loading={routeLoading}
                      onDropdownVisibleChange={(open) => {
                        if (open && routeOptions.length === 0) loadRoutes();
                      }}
                      filterOption={(input, option) =>
                        option && option.label ? option.label.toLowerCase().includes(input.toLowerCase()) : false
                      }
                      onChange={handleRouteChange}
                      placeholder={labels.route}
                    />
                  </div>
                  <div className="margin-dev-aspect-checkbox">
                    <Checkbox
                      checked={routeAspect}
                      disabled={serverAspectDisabled.route || aspectDisabled.route}
                      onChange={(e) => handleAspectChange('route', e.target.checked)}
                      aria-label={labels.route}
                    />
                  </div>
                </div>
              </Space>
            </Card>
          </Col>

          <Col xs={24} md={12} xl={5}>
            <Card title={labels.viewColumns}>
              <div className="margin-dev-columns">
                {labels.columns.map((item) => (
                  <div key={item.key} className="margin-dev-columns-row">
                    <span>{item.label}</span>
                    <Checkbox
                      checked={!!viewColumns[item.key]}
                      disabled={item.key === 'view_other_sum' ? viewOtherSumDisabled : false}
                      onChange={(e) =>
                        setViewColumns((prev) => ({
                          ...prev,
                          [item.key]: e.target.checked,
                        }))
                      }
                    />
                  </div>
                ))}
              </div>
            </Card>
          </Col>

          <Col xs={24} md={12} xl={5}>
            <Card title={labels.options}>
              <div className="margin-dev-option-row">
                <span style={{ flex: '1 1 auto' }}>{labels.onlyTotal}</span>
                <Checkbox checked={onlyTotal} onChange={(e) => setOnlyTotal(e.target.checked)} />
              </div>
              <div className="margin-dev-option-row">
                <span style={{ flex: '1 1 auto' }}>{labels.itogBySpec}</span>
                <Checkbox checked={itogBySpec} onChange={(e) => setItogBySpec(e.target.checked)} />
              </div>
              <div className="margin-dev-option-row">
                <span style={{ flex: '1 1 auto' }}>{labels.itogByUser}</span>
                <Checkbox
                  checked={itogByUser}
                  disabled={!itogBySpec}
                  onChange={(e) => setItogByUser(e.target.checked)}
                />
              </div>
              <div className="margin-dev-option-row">
                <span style={{ flex: '1 1 auto' }}>{labels.itogByProduct}</span>
                <Checkbox
                  checked={itogByProduct}
                  disabled={!itogByUser}
                  onChange={(e) => setItogByProduct(e.target.checked)}
                />
              </div>
              <div className="margin-dev-option-row" style={{ marginTop: 6 }}>
                <span style={{ flex: '1 1 auto' }}>{labels.getNotBlock}</span>
                <Checkbox checked={getNotBlock} onChange={(e) => setGetNotBlock(e.target.checked)} />
              </div>
            </Card>
          </Col>
        </Row>

        <div className="margin-dev-actions">
          <Button onClick={() => submitAction(actionClean)}>{labels.buttonClear}</Button>
          <Button type="primary" disabled={!canGenerate} onClick={() => submitAction(actionGenerate)}>
            {labels.buttonForm}
          </Button>
          <Button disabled={!canGenerate} onClick={() => generateExcel(actionExcel)}>
            {labels.buttonExcel}
          </Button>
        </div>

        <div className="margin-dev-grid">
          <iframe className="margin-dev-grid-frame" src={gridUrl} title="Margin Grid" style={{ height: 'calc(100vh - 420px)' }} />
        </div>
      </div>
    </ConfigProvider>
  );
};

export default MarginDevApp;
